/* Global */

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, Helvetica, sans-serif;
	background-color: #171717;
	overflow: hidden;
}

/* ToDo List */

.todo {
	display: block;
	width:  100%;
	line-height: 1.5rem;
	padding: 0.6rem 1rem;
	border-bottom: 1px rgba(23, 23, 23, 0.1) solid;
}

.todo:hover {
	filter: brightness(0.9);
}

.todo .check {
	display: none;
}

.todo:hover .check {
	display: inline;
	line-height: 1.5rem;
	margin-right: 0.9rem;
	cursor: pointer;
}

.todoText {
	display: inline;
	line-height: 1.5rem;
	width: auto;
	color: #d5d5d6;
	font-size: 1rem;
	outline: none;
	border:  none;
	background: none;
}

.delete {
	display: none;
	line-height: 1.5rem;
	color: #ededed;
	background: none;
	border: none;
	font-size: 2rem;
	float: right;
	padding: none;
	cursor: pointer;
}

.todo:hover .delete {
	display: inline;
}

/* Add ToDo */

.toggle {
	z-index: -1;
	position: absolute;
	top: 0;
	color: #ededed;
	background: #171717;
	height: 30.3rem;
	width: 100%;
	cursor: crosshair;
	border-bottom: solid 1px #111;
}

.field {
	height: 2.75rem;
	width: 100%;
	padding: 0.3rem 0.94rem;
	font-size: 1rem;
	outline: none;
	color: #ededed;
	background: #d74c32;
	border: none;
	border-bottom: 1px rgba(23, 23, 23, 0.2) solid;
}

.field:hover {
	filter: brightness(0.9);
}

input::placeholder {
	color: rgba(237, 237, 237, 0.4);
}